import React from 'react';
import SubSection from "./SubSection.js"

function IconSection(props) {
    //console.log("Section");
    //console.log(props);
    return (<div className="section iconSection">
        <img src={props.content.icon} alt="" />
        <h2 className="sectionHeader">{props.content.sectionTitle}</h2>
        <div className="sectionContent">
            {props.content.subsections ? props.content.subsections.map( subsection => (<SubSection key={subsection.sectionTitle} content={subsection} />)) : ""}
            <br />
            {props.content.text}
            <br />
            {props.content.footLink && <a href={props.content.footLink.link}>{props.content.footLink.text}</a>}
        </div>
    </div>);
}

export default IconSection;