import React from 'react';

function Section(props) {
    //console.log("Section");
    //console.log(props);
    return (<div className="section">
        <h2 className="sectionHeader">{props.content.sectionTitle}</h2>
        <div className="sectionContent">
            <img src={props.content.image} alt="" width="100%" />
            <br />
            {props.content.text}
            <br />
            {props.content.fields ? props.content.fields.map( field => (<div key={field.name}>
                <span className="field">{field.name}</span>
                <span className="value">{field.value}</span>
            </div>)) : ""}
            {props.content.footLink && <a href={props.content.footLink.link}>{props.content.footLink.text}</a>}
        </div>
    </div>);
}

export default Section;