import React from 'react';

function GalleryEnlargedImage(props) {
    if(!props.image) {
        return (null);
    }
    return (<div className="overlay" onClick={() => props.onClose()}>
                <button className="leftNav" onClick={e => {
                    e.stopPropagation();
                    props.onLeft();
                }}>&lt;</button>
                <div className="enlargedImage">
                    <img src={props.image.image} alt="" />
                </div>
                <button className="rightNav" onClick={e => {
                    e.stopPropagation();
                    props.onRight();
                }}>&gt;</button>
                <button className="closeButton" onClick={() => props.onClose()}>X</button>
            </div>);
}

export default GalleryEnlargedImage;