import React from 'react';

function SubSection(props) {
    //console.log("Section");
    //console.log(props);
    return (<div className="section subSection">
        <h3 className="sectionHeader" dangerouslySetInnerHTML={{ "__html": props.content.sectionTitle}} />
        <div className="sectionContent">
            {props.content.lines ? (props.content.lines.map(line => { return (<div className="textLine" key={line.key+"-"+line.text}>{line.text}</div>);})) : (null) }
            {props.content.text}
            <br />
            {props.content.footLink && <a href={props.content.footLink.link}>{props.content.footLink.text}</a>}
        </div>
    </div>);
}

export default SubSection;