import React from 'react';
import RSVPField from './RSVPField';

function RSVPGuest(props) {
    return (<div>
        <RSVPField schema={props.schema.find(element => element.field === "preferredName")} 
            value={props.guest.preferredName ? props.guest.preferredName : props.guest.firstName+" "+props.guest.lastName} onChange={(field, value) => props.onChange(props.guest.id, field, value)}
            disabled={!props.guest.canRSVP} />
        <RSVPField schema={props.schema.find(element => element.field === "attending")} 
            value={props.guest.attending} onChange={(field, value) => props.onChange(props.guest.id, field, value)}
            disabled={!props.guest.canRSVP} />
        <RSVPField schema={props.schema.find(element => element.field === "food")} 
            value={props.guest.food} onChange={(field, value) => props.onChange(props.guest.id, field, value)} 
            hide={props.guest.attending === "false"} disabled={!props.guest.canRSVP} />
        <RSVPField schema={props.schema.find(element => element.field === "pronouns")} 
            value={props.guest.pronouns} onChange={(field, value) => props.onChange(props.guest.id, field, value)} 
            hide={props.guest.attending === "false"} disabled={!props.guest.canRSVP} />
        <RSVPField schema={props.schema.find(element => element.field === "addPronouns")} 
            value={props.guest.addPronouns} onChange={(field, value) => props.onChange(props.guest.id, field, value)}
            hide={!props.guest.pronounsNotListed} disabled={!props.guest.pronounsNotListed || !props.guest.canRSVP} />
        <RSVPField schema={props.schema.find(element => element.field === "musicRequests")} 
            value={props.guest.musicRequests} onChange={(field, value) => props.onChange(props.guest.id, field, value)} 
            hide={props.guest.attending === "false"} disabled={!props.guest.canRSVP} />
    </div>);
}

export default RSVPGuest;