import React from 'react';

function AttractionEntry(props) {
    return (<div className="attractionEntry">
        <div className="attractionImageContainer">
            <img src={props.content.image} alt="" className="attractionLogo" />
        </div>
        <div className="attractionDetails">
            <div className="attractionTitle">{props.content.name}</div>
            <div className="attractionAddress1">{props.content.address1}</div>
            <div className="attractionAddress2">{props.content.address2}</div>
            <div className="attractionDescription">
                {props.content.description}
                <br />
                <a href={props.content.link.link}>{props.content.link.text}</a>    
            </div>
        </div>
    </div>);
}

export default AttractionEntry;