import React from 'react';
import Section from './Section.js';
import HeaderImage from './HeaderImage.js';
import GallerySection from './GallerySection.js';
import EmbedSection from './EmbedSection.js';
import AttractionsSection from './AttractionsSection.js';
import Announcement from './Announcement.js';
import IconSection from './IconSection.js';
import SubSection from './SubSection.js';
import FaqSection from './FaqSection.js';
import RSVPForm from './RSVPForm.js';

function Page(props) {
    return (props.pageData.content.map(content => {
        switch (content.contentType){
            case "headerImage":
                return (<HeaderImage key={content.contentType+"-"+content.sectionTitle} content={content} />);
            case "section":
                return (<Section key={content.contentType+"-"+content.sectionTitle} content={content} />);
            case "gallerySection":
                return (<GallerySection key={content.contentType+"-"+content.sectionTitle} content={content} />);
            case "embedSection":
                return (<EmbedSection key={content.contentType+"-"+content.sectionTitle} content={content} />);
            case "attractionsSection":
                return (<AttractionsSection key={content.contentType+"-"+content.sectionTitle} content={content} />);
            case "iconSection":
                return (<IconSection key={content.contentType+"-"+content.sectionTitle} content={content} />);
            case "subSection":
                return (<SubSection key={content.contentType+"-"+content.sectionTitle} content={content} />);
            case "faqSection":
                return (<FaqSection key={content.contentType+"-"+content.key} content={content} />);
            case "rsvpForm":
                return (<RSVPForm key={content.contentType+"-"+content.key} content={content} sessionId={props.sessionId} />);
            case "announcement":
            default:
                return (<Announcement key={content.contentType+"-"+content.sectionTitle} content={content} />);
        }
    }));
}

export default Page;
