import React from 'react';
import Faq from './Faq.js';

function Section(props) {
    //console.log("Section");
    //console.log(props);
    return (<div className="section faqSection">
            {props.content.faqs ? props.content.faqs.map( faq => (<Faq key={faq.number+"-"+faq.question} content={faq} />)) : ""}
    </div>);
}

export default Section;