import React from 'react';
import { Component } from 'react';


export default class RSVPField extends Component{

    constructor(props){
        super(props);

        this.state = {
            displayHelp: false
        }

    }

    toggleHelp(display){
        this.setState({displayHelp: display});
    }

    renderField(){
        let displayValue = this.props.value;
        let type = this.props.schema.type;
        if (type === "select") {
            displayValue = this.props.schema.options[displayValue];
        }
        if (this.props.disabled){
            type = "display"
        }
        switch (type){
            case "text":
                return (<input type="text" value={this.props.value} onChange={event => this.props.onChange(this.props.schema.field, event.target.value)} />);
            case "select":
                return (<select value={this.props.value} onChange={event => this.props.onChange(this.props.schema.field, event.target.value)}>
                    {Object.entries(this.props.schema.options).map(option => (<option key={option[0]} value={option[0]}>{option[1]}</option>))}
                </select>);
            case "display":
            default:
                return (<span>{displayValue}</span>);
        }
    }

    render(){
        return ( <div className={this.props.hide ? "field hide": "field"}>
            <div className={this.state.displayHelp ? "fieldHelpInfo" : "hide"}>
                {this.props.schema.help}    
            </div>
            <label>
                {this.props.schema.label}
                {this.props.schema.required ? (<span className="required">*</span>) : (null)}
                {this.props.schema.help ? (<span className="material-icons" 
                    onClick={() => {this.toggleHelp(!this.state.displayHelp)}} >{'\ue88e'}</span>) : (null)}
                {this.renderField(this.props.schema.type, this.props)}
            </label>
        </div>);
    }
}
