import React from 'react';

function Faq(props) {
    return (<div className="faq">
        <div className="faqHeader">
            <div id={props.content.number} className="faqNumber">{props.content.number}</div>
            <span className="question">{props.content.question}</span>
        </div>
        <div className="answerBlock">
            <span className="answer" dangerouslySetInnerHTML={{ "__html": props.content.answer}} />
        </div>
    </div>);
}

export default Faq;