import React from 'react';
import {Component} from 'react';
import GalleryImage from './GalleryImage.js';
import GalleryEnlargedImage from './GalleryEnlargedImage.js';

export default class GallerySection extends Component{

    constructor(props) {
        super(props);

        this.openEnlargedImage = this.openEnlargedImage.bind(this);
        this.closeEnlargedImage = this.closeEnlargedImage.bind(this);
        this.navLeft = this.navLeft.bind(this);
        this.navRight = this.navRight.bind(this);
        this.state = {
            openImage: null
        };
    }

    openEnlargedImage(image) {
        this.setState({openImage: image});
    }

    closeEnlargedImage() {
        this.setState({openImage: null});
    }

    navLeft() {
        if (!this.state.openImage){
            return;
        }
        this.setState((state, props) => {
            const openIndex = props.content.images.indexOf(state.openImage);
            let leftImageIndex = (openIndex - 1 + props.content.images.length) % props.content.images.length;
            while (!props.content.images[leftImageIndex].display) {
                leftImageIndex = (leftImageIndex - 1 + props.content.images.length) % props.content.images.length;
            }
            return ({ openImage: props.content.images[leftImageIndex]})
        });
    }

    navRight() {
        if (!this.state.openImage){
            return;
        }
        this.setState((state, props) => {
            const openIndex = props.content.images.indexOf(state.openImage);
            let rightImageIndex = (openIndex + 1 + props.content.images.length) % props.content.images.length;
            while (!props.content.images[rightImageIndex].display) {
                rightImageIndex = (rightImageIndex - 1 + props.content.images.length) % props.content.images.length;
            }
            return ({ openImage: props.content.images[rightImageIndex]})
        });
    }

    componentDidMount() {
        
    }

    render() {
        return (
        <div className="section">
            <h2 className="sectionHeader">{this.props.content.sectionTitle}</h2>
            <div className="sectionContent">
                {this.props.content.images.map(image => (<GalleryImage key={image.id} content={image} onClick={() => this.openEnlargedImage(image)} />))}
            </div>
            <GalleryEnlargedImage image={this.state.openImage} onClose={() => this.closeEnlargedImage()} onLeft={() => this.navLeft()} onRight={() => this.navRight()} />
        </div>
        );
    }
};