import React from 'react';
import AttractionEntry from './AttractionEntry.js';

function AttractionsSection(props) {
return (<div className="section">
        <h2 className="sectionHeader">{props.content.sectionTitle}</h2>
        <div className="sectionContent">
            {props.content.attractions.map( attraction => <AttractionEntry key={attraction.name} content={attraction} />)}
        </div>    
    </div>);
}

export default AttractionsSection;