import React from 'react';

function Announcement(props) {
    //console.log("Announcement");
    //console.log(props);
    return (<div className="section">
        <div className="sectionContent">
            <h3 className="announcement">{props.content.line1}</h3>
            <h3 className="announcementDetails">{props.content.line2}</h3>
            {props.content.details.map( detail => (<div key={detail.substring(10,20)+"|"+detail.length+"|"+Math.round(Math.random()*10000)} className="announcementDescriptionDetail">{detail}</div>))}
        </div>
    </div>);
}

export default Announcement;